import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {RouterModule, Routes} from '@angular/router';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {VendorSignUpComponent} from './vendors/vendor-signup.component';
import {InternalUsersComponent} from './internal-users/internal-users.component';
import {CustomErrorModule} from '../../shared/custom-error/custom-error.module';
import {LoaderModule} from '../../shared/loader/loader.module';

export const routes: Routes = [
    {path: '', redirectTo: 'vendor', pathMatch: 'full'},
    {path: 'mda-staff', component: InternalUsersComponent},
    {path: 'vendor', component: VendorSignUpComponent}
];

@NgModule({
    imports: [
        CommonModule,
        FormsModule, CustomErrorModule, LoaderModule,
        ReactiveFormsModule,
        RouterModule.forChild(routes),
    ],
    declarations: [
        VendorSignUpComponent,
        InternalUsersComponent
    ]
})
export class RegisterModule {
}
