import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {Location} from '@angular/common';
import {ICity, IState, IVendor, IVendorDataFromApi} from '../../../interfaces/interfaces';
import {VendorDataService} from '../../../services/vendor-data.service';
import {Constants} from '../../../app.constants';
import Swal from 'sweetalert2';


@Component({
    selector: 'app-vendor-signup',
    templateUrl: './vendor-signup.component.html',
    styleUrls: ['./vendor-signup.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class VendorSignUpComponent implements OnInit {
    public vendorForm: FormGroup;
    frontendUrl = 'http://' + window.location.hostname + '/activate';
    states: IState[] = [];
    cities: ICity[] = [];
    errorMessage: '';
    loading = false;
    emailIsTaken: string;
    phoneIsTaken: string;
    usernamelIsTaken: string;
    rclIsTaken: string;
    taxlIsTaken: string;
    ereqLoading: boolean;
    ureqLoading: boolean;
    preqLoading: boolean;
    rcreqLoading: boolean;
    treqLoading: boolean;
    emailMessage: string;
    usernameMessage: string;
    phoneMessage: string;
    rcMessage: string;
    taxMessage: string;

    hasConfirmedRCNumber = false;
    justContinue = false;
    apiError = '';
    loadingFromApi = false;
    display = 'registration';

    constructor(private router: Router, private formBuilder: FormBuilder,
                private vendorDataService: VendorDataService, private _Location: Location) {
    }


    ngOnInit() {
        this.buildForm();
        this.getStates();
    }

    buildForm() {
        this.vendorForm = this.formBuilder.group({
            business_name: ['', Validators.required],
            rc_number: ['', Validators.required],
            tax_identification_number: ['', Validators.required],
            business_description: ['', Validators.required],
            first_name: ['', Validators.required],
            last_name: ['', Validators.required],
            middle_name: [''],
            email: ['', Validators.compose([Validators.required, Validators.email])],
            email1: ['', Validators.compose([Validators.email])],
            website: [''],
            phone: ['', Validators.compose([Validators.required, Validators.minLength(8)])],
            username: ['', Validators.required],
            password: ['', Validators.compose([Validators.required, Validators.minLength(8)])],
            password_confirmation: ['', Validators.required],
            address_line_1: ['', Validators.required],
            state_id: ['', Validators.required],
            city_id: ['', Validators.required],
            address_line_2: [''],
            pobox_number: [''],
            frontend_url: [this.frontendUrl]
        });
    }

    getStates() {
        this.vendorDataService.getStates()
            .subscribe((states: IState[]) => {
                this.states = states
            }, (err) => {
                this.errorMessage = Constants.handleError(err, this.router);
            });
    }

    hitBppApi(rcNumber: string) {
        this.apiError = '';
        this.errorMessage = '';
        if (rcNumber) {
            this.loadingFromApi = true;
            this.vendorDataService.fetchVendorDataFromBPPApi(rcNumber).subscribe((response: IVendorDataFromApi[]) => {
                this.loadingFromApi = false;
                if (response.length) {
                    const data = response[0];
                    this.vendorForm.patchValue({
                        business_name: data.companyName,
                        address_line_1: data.headOfficeAddressStreet,
                        pobox_number: data.headOfficeAddressPobox,
                        tax_identification_number: data.tinNumber,
                        rc_number: data.rcNumber,
                        email: data.companyContactEmail || data.companyContactAlternativeEmail,
                        email1: data.companyContactAlternativeEmail,
                        website: data.website,
                        phone: data.companyContactPhone1 || data.companyContactPhone2
                    });
                    this.hasConfirmedRCNumber = true;
                } else {
                    Swal.fire({
                        title: 'Sorry!',
                        html: 'It seemed like your company is not BPP compliant.<br>Do you want to continue?',
                        type: 'question',
                        confirmButtonText: 'Continue',
                        showConfirmButton: true,
                        showCancelButton: true,
                        cancelButtonText: 'Cancel'
                    }).then(goOn => {
                        if (goOn.value) {
                            this.justContinue = true;
                        }
                    });
                }
            }, (err) => {
                this.apiError = Constants.handleError(err, this.router);
                this.loadingFromApi = false;
            });
        } else {
            this.apiError = 'Please provide your RC Number';
        }
    }

    onSubmit({value, valid}: { value: IVendor, valid: boolean }) {
        this.loading = true;
        if (value) {
            this.vendorDataService.addVendor(value).subscribe((vendor: IVendor) => {
                if (vendor) {
                    this.display = 'successful';
                } else {
                    this.loading = false;
                }
            }, (err) => {
                Constants.handleError(err, this.router, true);
                this.loading = false;
            });
        }
    }

    checkEmail(email) {
        email = email.trim();
        if (!email) return;
        this.emailMessage = '';
        this.ereqLoading = true;
        const field = 'email';
        this.vendorDataService.IfExists(field, email).subscribe((status) => {
            this.emailIsTaken = status;
            this.ereqLoading = false;
            this.emailMessage = 'This email already exists';
        }, (err) => {
            this.emailMessage = Constants.handleError(err, this.router);
        });
    }

    checkUsername(username) {
        username = username.trim();
        if (!username) return;
        this.usernameMessage = '';
        this.ureqLoading = true;
        const field = 'username';
        this.vendorDataService.IfExists(field, username).subscribe((status) => {
            this.usernamelIsTaken = status;
            this.ureqLoading = false;
            this.usernameMessage = 'This username already exists';
        }, (err) => {
            this.usernameMessage = Constants.handleError(err, this.router);
        });
    }

    checkPhone(phone) {
        this.phoneMessage = '';
        if (phone === undefined || phone.toString().trim() === '') {
            return;
        }
        this.preqLoading = true;
        const field = 'phone';
        this.vendorDataService.IfExists(field, phone).subscribe((status) => {
                this.phoneIsTaken = status;
                this.preqLoading = false;
                this.phoneMessage = 'This phone number already exists';
            }, (err) => {
                this.phoneMessage = Constants.handleError(err, this.router);
            }
        );
    }

    checkRCNumber(rc_number) {
        this.rcMessage = '';
        if (rc_number === undefined || rc_number.toString().trim() === '') {
            return;
        }
        this.rcreqLoading = true;
        const field = 'rc_number';
        this.vendorDataService.IfExists(field, rc_number)
            .subscribe((status) => {
                this.rclIsTaken = status;
                this.rcreqLoading = false;
                this.rcMessage = 'This RC Number already exists';
            }, (err) => {
                this.rcMessage = Constants.handleError(err, this.router);
            })

    }

    checkTaxID(tax_id: string) {
        tax_id = tax_id.trim();
        this.taxMessage = '';
        if (tax_id === undefined || tax_id.length < 12) {
            return;
        }
        this.treqLoading = true;
        const field = 'tax_identification_number';
        this.vendorDataService.IfExists(field, tax_id).subscribe((status) => {
                this.taxlIsTaken = status;
                this.treqLoading = false;
                this.taxMessage = 'This tax identification number already exists'
            }, (err) => {
                this.taxMessage = Constants.handleError(err, this.router);
            }
        )
    }


    showPassword(input: any): any {
        input.type = input.type === 'password' ? 'text' : 'password';
    }

    changeCities(v) {
        this.cities = [];
        this.vendorForm.patchValue({'city_id': ''});
        if (!v) {
            return;
        }

        this.states.forEach((state) => {
            if (state.id.toString() === v.toString()) {
                this.cities = state.cities;
            }
        });
    }

    ngAfterViewInit() {
        document.getElementById('preloader').classList.add('hide');
    }
}
