import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {Location} from '@angular/common';
import {Grades, IInternalUser, IMinistry} from '../../../interfaces/interfaces';
import {UserDataService} from '../../../services/user.service';
import {VendorDataService} from '../../../services/vendor-data.service';
import {GradeService} from '../../../services/grade.service';
import {Constants} from '../../../app.constants';

@Component({
  selector: 'app-internal',
  templateUrl: './internal-users.component.html',
  styleUrls: ['./internal-users.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class InternalUsersComponent implements OnInit {
  public internalUserForm: FormGroup;
  frontendUrl = 'http://' + window.location.hostname + '/activate';
  errorMessage: string;
  loading = false;
  ereqLoading: boolean;
  emailIsTaken: string;
  emailMessage: string;
  ureqLoading: boolean;
  usernamelIsTaken: string;
  usernameMessage: string;
  url: string;
  institutions: IMinistry[] = [];
  parastatals: IMinistry[] = [];
  grades: Grades[] = [];
  selectedParastatal: any = false;
  selectedParastatalCode: any = '';

  display = 'registration';
  loadingData = false;

  constructor(private router: Router,
              private userDataService: UserDataService,
              private vendorDataService: VendorDataService,
              private gradeService: GradeService,
              private _Location: Location,
              private formBuilder: FormBuilder) {
  }

  ngOnInit() {
    this.url = Constants.url;
    this.buildForm();
    this.getRegistrationData();
  }

  buildForm() {
    this.internalUserForm = this.formBuilder.group({
      first_name: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
      last_name: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
      email: ['', Validators.compose([Validators.required, Validators.email])],
      phone: ['', Validators.compose([Validators.required, Validators.minLength(8), Validators.maxLength(20)])],
      username: ['', Validators.required],
      password: ['', Validators.compose([Validators.required, Validators.minLength(8)])],
      institution_id: ['Institution', Validators.required],
      grade_id: ['', Validators.required],
      parastatal_id: ['', Validators.required],
      parastatal_code: ['', Validators.required],
      job_title: ['', Validators.required],
      employed_date: ['', Validators.required],
      frontend_url: [this.frontendUrl]
    })
  }

  getRegistrationData() {
    this.loadingData = true;
    this.userDataService.getMDARegistrationData().subscribe((data: any) => {
      this.institutions = data.institutions;
      this.grades = data.grades;
      this.loadingData = false;
    }, (err) => {
      this.errorMessage = Constants.handleError(err, this.router);
      this.loadingData = true;
    })
  }

  changeParastatals(value) {
    this.parastatals = [];
    if (value) {
      for (let x = 0; x < this.institutions.length; x++) {
        if (this.institutions[x].id.toString() === value.toString()) {
          this.parastatals = this.institutions[x].parastatals;
          break;
        }
      }
    }
  }


  onSubmit({value, valid}: { value: IInternalUser, valid: boolean }) {
    this.loading = true;
    //   console.log(value);
    if (value) {
      this.userDataService.addUser(value)
        .subscribe((internalUser: IInternalUser) => {
            if (internalUser) {
              this.display = 'successful';
            } else {
              this.loading = false;
            }
          }, (err) => {
            this.loading = false;
            Constants.handleError(err, this.router, true);
          }
        );
    }
  }

  checkEmail(email) {
    email = email.trim();
    if (!email) {
      return;
    }
    this.ereqLoading = true;
    const field = 'email'
    this.vendorDataService.IfExists(field, email)
      .subscribe((status) => {
          this.emailIsTaken = status;
          this.ereqLoading = false;
          this.emailMessage = 'This email is already exists';
        },
        (err) => {
          this.errorMessage = Constants.handleError(err, this.router);
        })
  }

  checkUsername(username) {
    this.ureqLoading = true;
    username = username.trim();
    if (!username) {
      return;
    }
    const field = 'username';
    this.vendorDataService.IfExists(field, username).subscribe((status) => {
      this.usernamelIsTaken = status;
      this.ureqLoading = false;
      this.usernameMessage = 'This username already exists';
    }, (err) => {
      this.errorMessage = Constants.handleError(err, this.router);
    })

  }

  showPassword(input: any): any {
    input.type = input.type === 'password' ? 'text' : 'password';
  }

  ngAfterViewInit() {
    document.getElementById('preloader').classList.add('hide');
  }

  updateParastatalCode(id) {
    const institution = this.internalUserForm.get('institution_id').value;
    alert(institution);
    const institiondRec = this.institutions.find(e => e.id === institution);
    if (institiondRec) {
      this.selectedParastatal = institiondRec.parastatals.find(ee => ee.id === id);
    }

    this.internalUserForm.patchValue({
      parastatal_code: this.selectedParastatal ? this.selectedParastatal.code : '',
    });

    this.selectedParastatalCode = this.selectedParastatal ? this.selectedParastatal.code : '';
  }
}
